<template>
  <div
    class="cuadro w-6 h-6"
    :disabled="array.status === 'NINGUNO' ? true : false"
    :class="[
      array.status === 'NINGUNO' ? 'cursor-not-allowed' : 'cursor-pointer',
      array.status === 'SOLUCIONADO' ? 'SOLUCIONADO' : '',
      array.status === 'PROCESO' && array.semana < semanaActual
        ? 'PROCESOATRASADO'
        : '',
      array.status === 'PROCESO' && !array.semana < semanaActual
        ? 'PROCESO'
        : '',
    ]"
    @click="abrirEnlace(array)"
  >
    <span
      v-if="array.status === 'PROCESO' && array.semana < semanaActual"
      class="
        flex
        justify-center
        items-center
        font-bold
        animate-ping
        text-sm text-white
      "
    >
      ?
    </span>
  </div>
</template>

<script>
import { getISOWeek } from "date-fns";
import { useStore } from "vuex";

export default {
  props: {
    array: { type: Object, required: false },
  },

  setup(props) {
    const store = useStore();
    const Swal = require("sweetalert2");

    const fechaHoy = new Date();

    const semanaActual = getISOWeek(
      new Date(fechaHoy.getFullYear(), fechaHoy.getMonth(), fechaHoy.getDate())
    );

    const abrirEnlace = (item) => {
      localStorage.setItem("URL", "");

      if (item.status === "NINGUNO") return;

      if (item.idEquipo > 0 && item.semana > 0 && item.idOT > 0) {
        localStorage.setItem(
          "URL",
          `0;0;${item.idEquipo};${item.semana};${item.idPlan}; ${item.año}`
        );
        window.open(
          "../OT/index.php",
          "OT",
          "directories=no, location=no, menubar=no, scrollbars=yes, statusbar=no, tittlebar=no, width=1200px, height=650px"
        );
        return;
      }

      if (item.status === "PLANIFICADO")
        Swal.fire({
          title: "¡LA OT NO ESTA CREADA AÚN!",
          text: "¿Desea poner en PROCESO esta OT?",
          icon: "question",
          iconColor: "#fff",
          background: "#2c3e50",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Si, poner en PROCESO",
          cancelButtonText: "No, Cancelar",
        }).then(async (result) => {
          if (result.isConfirmed) {
            const data = {
              idDestino: localStorage.getItem("idDestino"),
              idUsuario: localStorage.getItem("usuario"),
              action: "programarMP",
              idSemana: item.idSemana,
              idProceso: item.idProceso,
              idEquipo: item.idEquipo,
              semanaX: item.semana,
              accionMP: "GENERAROT",
              idPlan: item.idPlan,
              numeroSemanas: 0,
              año: item.año,
            };

            console.log(data);

            const response = await store.dispatch("accionesUsuario", data);
            if (response.response) {
              props.array.status = "PROCESO";
              if (response.data.idOT > 0) props.array.idOT = response.data.idOT;

              Swal.fire({
                title: "OT INICIADA!",
                html: `Se ha generado una OT en la semana: ${item.semana}`,
                icon: "success",
                background: "#2c3e50",
                customClass: {
                  popup: "colored-toast",
                  backdrop: "swal2-backdrop-show",
                  icon: "swal2-icon-show",
                },
                iconColor: "#fff",
              });
            }

            if (!response.response)
              Swal.fire({
                title: "OT NO GENERADA!",
                html: `Comprueba los datos del Plan`,
                icon: "error",
                background: "#2c3e50",
                customClass: {
                  popup: "colored-toast",
                  backdrop: "swal2-backdrop-show",
                  icon: "swal2-icon-show",
                },
                iconColor: "#fff",
              });
          }
        });
    };

    return { semanaActual, abrirEnlace };
  },
};
</script>

<style scoped>
.cuadro {
  border-top-width: 2px;
  border-bottom-width: 2px;
  border-left-width: 2px;
}

.cuadro:last-child {
  border-right-width: 2px;
}

.NINGUNO {
  background-color: white;
}

.PROCESO {
  background-color: rgb(255, 255, 118) !important;
}
.PROCESOATRASADO {
  background-color: rgb(231, 92, 87) !important;
}

.SOLUCIONADO {
  background-color: rgb(115, 238, 115) !important;
}

.PLANIFICADO {
  background-color: rgb(98, 157, 252) !important;
}
</style>
