<template>
  <div class="w-full relative">
    <div
      class="fila relative"
      :class="{
        'border-yellow-500': status === 'PROCESO',
        'border-green-500': status === 'SOLUCIONADO',
        'border-blue-500': status === 'PLANIFICADO',
        'border-gray-500': status === 'Varios',
      }"
      @click="comprobarArbirEnlace(array)"
    >
      <!-- ICONO SEMANA -->
      <div
        class="
          flex flex-row
          justify-center
          items-center
          text-center
          w-11.5
          h-4
          text-xxs
          rounded-full
          font-semibold
          absolute
          top-2
          right-2
          p-2
        "
        v-if="array.totalPlaneaciones == 1"
        :class="{
          'bg-yellow-100 text-yellow-500': status === 'PROCESO',
          'bg-green-100 text-green-500': status === 'SOLUCIONADO',
          'bg-blue-100 text-blue-500': status === 'PLANIFICADO',
        }"
      >
        SEM {{ semana }}
      </div>

      <!-- SECCIÓN -->
      <div class="w-1/2 truncate md:w-seccion">
        <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
          Sección:
        </h1>
        <h2 class="font-bold uppercase text-xs md:text-center w-full">
          {{ array.seccion }}
        </h2>
      </div>

      <!-- SUBSECCIÓN -->
      <div class="w-1/2 truncate md:w-1/6">
        <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
          Subsección:
        </h1>
        <h2 class="font-bold uppercase text-xs md:text-center w-full">
          {{ array.subseccion }}
        </h2>
      </div>

      <!-- TIPO DE EQUIPO -->
      <div class="w-1/2 truncate md:w-1/6">
        <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
          Tipo:
        </h1>
        <h2 class="font-bold uppercase text-xs md:text-center w-full">
          {{ array.tipo }}
        </h2>
      </div>

      <!-- MÁS DE UN PLAN -->
      <div class="w-1/2 truncate md:w-1/6">
        <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
          Plan:
        </h1>
        <!-- UN PLAN -->
        <h2
          v-if="array.totalPlaneaciones == 1"
          class="font-bold uppercase text-xs md:text-center w-full"
        >
          {{ frecuencia }}
        </h2>

        <!-- MÁS DE UN PLAN -->
        <h2
          v-else
          class="font-bold uppercase text-xs md:text-center w-full text-red-300"
        >
          Varios ({{ array.totalPlaneaciones }})
        </h2>
      </div>

      <!-- NOMBRE DEL EQUIPO -->
      <div class="w-full md:w-xx">
        <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
          Equipo:
        </h1>
        <h2 class="font-bold uppercase text-xs">{{ array.equipo }}</h2>
      </div>
      <div class="w-full hidden md:flex md:w-1/6">
        <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
          Estado:
        </h1>
        <h2
          v-if="array.totalPlaneaciones == 1"
          class="font-bold uppercase text-xs md:text-center w-full"
          :class="{
            'text-yellow-500': status === 'PROCESO',
            'text-green-500': status === 'SOLUCIONADO',
            'text-blue-500': status === 'PLANIFICADO',
          }"
        >
          {{ status }}
        </h2>
        <button
          @click="showhijos = !showhijos"
          v-else
          class="
            w-3/4
            mx-auto
            rounded
            bg-purple-100
            text-purple-500 text-xs
            py-1
            focus:outline-none
          "
        >
          <span v-if="!showhijos">VISUALIZAR</span>
          <span v-if="showhijos">OCULTAR</span>
        </button>
      </div>

      <div v-if="array.totalPlaneaciones > 1" class="w-full md:hidden">
        <button
          @click="showhijos = !showhijos"
          class="
            w-full
            mx-auto
            rounded
            bg-purple-100
            text-purple-500 text-xs
            py-1
            focus:outline-none
          "
        >
          <span v-if="!showhijos">VISUALIZAR</span>
          <span v-if="showhijos">OCULTAR</span>
        </button>
      </div>
    </div>

    <!-- HIJOS -->
    <div class="w-full" v-if="array.totalPlaneaciones > 1 && showhijos">
      <div
        v-for="(item, index) in array.planeaciones"
        :key="index"
        class="
          cursor-pointer
          mb-2
          md:mb-0
          hover:bg-gray-100
          md:rounded-r-none
          flex flex-row
          items-center
          flex-wrap
          justify-start
          border-l-4
          rounded-r-md
          px-2
          py-2
          md:py-5
          w-full
          text-left
          relative
        "
        :class="{
          'border-yellow-500': item.status === 'PROCESO',
          'border-green-500': item.status === 'SOLUCIONADO',
          'border-blue-500': item.status === 'PLANIFICADO',
        }"
        @click="abrirEnlace(item, index)"
      >
        <!-- ICONO SEMANA -->
        <div
          class="
            flex flex-row
            justify-center
            items-center
            text-center
            w-11.5
            h-4
            text-xxs
            rounded-full
            font-semibold
            absolute
            top-2
            right-2
            p-2
          "
          :class="{
            'bg-yellow-100 text-yellow-500': item.status === 'PROCESO',
            'bg-green-100 text-green-500': item.status === 'SOLUCIONADO',
            'bg-blue-100 text-blue-500': item.status === 'PLANIFICADO',
          }"
        >
          SEM {{ item.semana }}
        </div>

        <!-- SECCIÓN -->
        <div class="w-1/2 truncate md:w-seccion">
          <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
            Sección:
          </h1>
          <h2 class="font-bold uppercase text-xs md:text-center w-full">
            {{ array.seccion }}
          </h2>
        </div>

        <!-- SUBSECCIÓN -->
        <div class="w-1/2 truncate md:w-1/6">
          <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
            Subsección:
          </h1>
          <h2 class="font-bold uppercase text-xs md:text-center w-full">
            {{ array.subseccion }}
          </h2>
        </div>

        <!-- TIPO -->
        <div class="w-1/2 truncate md:w-1/6">
          <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
            Tipo:
          </h1>
          <h2 class="font-bold uppercase text-xs md:text-center w-full">
            {{ array.tipo }}
          </h2>
        </div>

        <!-- FRECUENCIA -->
        <div class="w-1/2 truncate md:w-1/6">
          <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
            Plan:
          </h1>
          <h2 class="font-bold uppercase text-xs md:text-center w-full">
            {{ item.frecuencia }}
          </h2>
        </div>

        <!-- EQUIPO -->
        <div class="w-full md:w-xx">
          <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
            Equipo:
          </h1>
          <h2 class="font-bold uppercase text-xs">{{ array.equipo }}</h2>
        </div>

        <!-- STATUS -->
        <div class="w-full hidden md:flex md:w-1/6">
          <h1 class="md:hidden text-xxs uppercase font-bold text-gray-500">
            Estado:
          </h1>
          <h2
            class="font-bold uppercase text-xs md:text-center w-full"
            :class="{
              'text-yellow-500': item.status === 'PROCESO',
              'text-green-500': item.status === 'SOLUCIONADO',
              'text-blue-500': item.status === 'PLANIFICADO',
            }"
          >
            {{ item.status }}
          </h2>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import { useStore } from "vuex";
export default {
  props: {
    array: { type: Object, required: true },
  },

  setup(props) {
    const store = useStore();
    const Swal = require("sweetalert2");

    const status = ref("varios");
    const frecuencia = ref("");
    const semana = ref("");
    const showhijos = ref(false);

    if (props.array.planeaciones.length == 1) {
      status.value = props.array.planeaciones[0].status;
      frecuencia.value = props.array.planeaciones[0].frecuencia;
      semana.value = props.array.planeaciones[0].semana;
    }

    const abrirEnlace = (item, index) => {
      localStorage.setItem("URL", "");

      if (item.idEquipo > 0 && item.semana > 0 && item.idOT > 0) {
        localStorage.setItem(
          "URL",
          `0;0;${item.idEquipo};${item.semana};${item.idPlan};${item.año}`
        );
        window.open(
          "../OT/index.php",
          "OT",
          "directories=no, location=no, menubar=no, scrollbars=yes, statusbar=no, tittlebar=no, width=1200px, height=650px"
        );
        return;
      }

      if (item.status === "PLANIFICADO")
        Swal.fire({
          title: "¡LA OT NO ESTA CREADA AÚN!",
          text: "¿Desea poner en PROCESO esta OT?",
          icon: "question",
          iconColor: "#fff",
          background: "#2c3e50",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Si, poner en PROCESO",
          cancelButtonText: "No, Cancelar",
        }).then(async (result) => {
          if (result.isConfirmed) {
            const data = {
              idDestino: localStorage.getItem("idDestino"),
              idUsuario: localStorage.getItem("usuario"),
              action: "programarMP",
              idSemana: item.idSemana,
              idProceso: item.idProceso,
              idEquipo: item.idEquipo,
              semanaX: item.semana,
              accionMP: "GENERAROT",
              idPlan: item.idPlan,
              numeroSemanas: 0,
              año: item.año,
            };

            const response = await store.dispatch("accionesUsuario", data);

            if (response.response) {
              Swal.fire({
                title: "OT INICIADA!",
                html: `Se ha generado una OT en la semana: ${item.semana}`,
                icon: "success",
                background: "#2c3e50",
                customClass: {
                  popup: "colored-toast",
                  backdrop: "swal2-backdrop-show",
                  icon: "swal2-icon-show",
                },
                iconColor: "#fff",
              });

              if (response.data.idOT > 0)
                props.array.planeaciones[index].idOT = response.data.idOT;
            }

            if (props.array.planeaciones.length == 1) {
              status.value = "PROCESO";
              return;
            }

            if (props.array.planeaciones.length > 1) {
              props.array.planeaciones[index].status = "PROCESO";
              return;
            }

            if (!response.response)
              Swal.fire({
                title: "OT NO GENERADA!",
                html: `Comprueba los datos del Plan`,
                icon: "error",
                background: "#2c3e50",
                customClass: {
                  popup: "colored-toast",
                  backdrop: "swal2-backdrop-show",
                  icon: "swal2-icon-show",
                },
                iconColor: "#fff",
              });
          }
        });
    };

    const comprobarArbirEnlace = (array) => {
      if (array.planeaciones.length == 1) abrirEnlace(array.planeaciones[0], 0);
    };

    return {
      showhijos,
      status,
      frecuencia,
      semana,
      comprobarArbirEnlace,
      abrirEnlace,
    };
  },
};
</script>

<style scoped>
.fila {
  @apply cursor-pointer mb-2 md:mb-0 hover:bg-gray-100 md:rounded-r-none flex flex-row items-center flex-wrap justify-start bg-white shadow border-l-4 rounded-r-md px-2 py-2 md:py-5 w-full text-left;
}

@media (min-width: 768px) {
  .md\:w-xx {
    width: 20%;
  }
}
@media (min-width: 768px) {
  .md\:w-seccion {
    width: 10%;
  }
}
</style>
